/* 기본 스타일 */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'STUNNING-Bd', sans-serif; /* OTF 폰트 적용 */
  background-color: #f4f4f4;
  color: #333;
  line-height: 1.6;
}

.app {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

/* 메뉴 스타일 - 스크롤 시 고정 */
.navbar {
  background-color: #4e54c8;
  color: white;
  padding: 15px;
  text-align: left; /* 메뉴 왼쪽 정렬 */
  position: fixed; /* 메뉴 고정 */
  top: 0;
  width: 100%;
  z-index: 1000;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* 메뉴 아래 그림자 추가 */
}

.navbar-menu {
  display: flex;
  justify-content: flex-start; /* 메뉴 항목들을 왼쪽으로 정렬 */
  gap: 20px;
  list-style-type: none;
}

.navbar-menu li {
  font-size: 1rem;
}

.navbar-menu a {
  color: white;
  text-decoration: none;
  font-weight: bold;
}

.navbar-menu a:hover {
  text-decoration: underline;
}

/* 섹션 머릿글 간격 설정 */
section h1 {
  margin-bottom: 20px; /* 각 섹션의 헤딩 사이에 20px 간격 추가 */
}

/* 자격증, 프로젝트, 연락처 머릿글 간격 추가 */
#certifications h1, #projects h1, #contact h1 {
  margin-top: 50px; /* 자격증, 프로젝트, 연락처 머릿글 위에 50px 간격 추가 */
}

/* 프로젝트 카드와 머릿글 사이 간격 10px */
#projects h1 + .projects-list {
  margin-top: 10px; /* 프로젝트 머릿글과 카드 사이에 10px 간격 */
}

/* 프로필 섹션 */
.profile-container {
  display: flex;
  justify-content: flex-start;
  gap: 20px;
  align-items: center;
  margin-top: 150px; /* 메뉴 고정으로 인한 상단 공간 확보 */
}

.profile-image {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
}

.about-text {
  max-width: 500px;
}

.about-text h1 {
  font-size: 1.6em;
}

.about-text p {
  font-size: 1em;
}

/* 기술 스택 섹션 */
.tech-stack {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
  margin-top: 20px;
}

.tech-stack-item {
  background-color: #f0f0f0;
  color: #333;
  padding: 8px 15px;
  border-radius: 12px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  font-size: 1em;
  transition: background-color 0.3s ease, transform 0.3s ease; /* 부드러운 효과 추가 */
}

.tech-stack-item:hover {
  background-color: #4e54c8;
  color: white;
  transform: scale(1.1);
}

/* 프로젝트 카드 간격 */
.projects-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 30px; /* 카드 간격을 더 넓게 설정 */
}

/* 자격증 및 프로젝트 세부 항목 글씨 크기 줄이기 */
.certifications-details, .project-details {
  font-size: 0.9rem; /* 자격증 및 프로젝트 세부 항목 글씨 크기 작게 */
}

/* 반응형 디자인: 모바일 화면 */
@media (max-width: 768px) {
  .navbar-menu {
    flex-direction: row; /* 세로로 변경하지 않고 가로로 유지 */
  }

  .profile-container {
    flex-direction: column;
    align-items: center;
  }

  .profile-image {
    width: 80px;
    height: 80px;
  }

  .about-text h1 {
    font-size: 1.4em;
  }

  .about-text p {
    font-size: 0.9em;
  }

  .tech-stack-item {
    font-size: 1.2em; /* 모바일에서 글씨 크기 증가 */
  }

  #certifications h1, #projects h1, #contact h1 {
    margin-top: 50px; /* 모바일에서도 자격증, 프로젝트, 연락처 머릿글 간격 유지 */
  }

  .certifications-details, .project-details {
    font-size: 0.8rem; /* 모바일에서 자격증 및 프로젝트 세부 항목 글씨 크기 더 작게 */
  }
}

/* 아주 작은 화면 (모바일) */
@media (max-width: 480px) {
  .profile-image {
    width: 60px;
    height: 60px;
  }

  .about-text h1 {
    font-size: 1.2em;
  }

  .about-text p {
    font-size: 0.85em;
  }

  .navbar-menu li {
    font-size: 0.8rem;
  }

  .navbar {
    padding: 10px;
  }
}

/* @font-face 수정 */
@font-face {
  font-family: 'STUNNING-Bd';
  src: url('/public/font/STUNNINGOTF.otf') format('opentype');
  font-weight: normal;
  font-style: normal;
}
