/* 기본 메뉴 스타일 */
.navbar-menu ul {
  list-style-type: none;
  display: flex;
  gap: 20px;
  justify-content: flex-start;
}

.navbar-menu li {
  font-size: 1.1rem;
}

.navbar-menu a {
  color: white;
  text-decoration: none;
  font-weight: bold;
}

.navbar-menu a:hover {
  text-decoration: underline;
}

/* 모바일에서 메뉴가 항상 보이도록 */
@media (max-width: 768px) {
  .navbar-menu {
    flex-direction: row; /* 세로로 변경하지 않고 가로로 유지 */
  }

  .navbar-menu li {
    font-size: 0.8rem; /* 모바일 화면에서 폰트 크기 고정 */
  }
}
