.certifications-container {
    padding: 20px;
  }
  
  .certifications-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
  }
  
  .certification-card {
    background-color: #ffffff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .certification-card h2 {
    font-size: 1.2em;
    margin-bottom: 10px;
  }
  
  .certification-card p {
    margin: 10px 0;
  }
  